// On initialise la latitude et la longitude de Lannion (centre de la carte)
        var lat = 48.73056610085155;
        var lon = -3.460834918664013;
        var macarte = null;
        var markerClusters; // Servira Ã  stocker les groupes de marqueurs

        // Nous initialisons un tableau city qui contiendra les "ville"
        let city = [];
        // Test function initPoint
        //var tests = [];
        //test[0] = Lat= 48.852969,Lon= 2.349903;
        function initLat(min, max){ //function initLatitude(min, max)
          min = min;
          max = max;
          return Math.random() * (max - min) + min;
        }
        function initLon(min, max){ //function initLongitude(min, max)
          min = min;
          max = max;
          return Math.random() * (max - min) + min;
        }

        // Fonction d'initialisation des points (randoms) sur la carte
        function initPoint() {
          for (let point = 0; point < 10; point++){

            // Pour la France et ses alentours:
            //Lat = initLat(42, 51);
            //Lon = initLon(-4, 8);

            // Pour la Bretagne et ses alentours:
            Lat = initLat(47.97, 48.5);
            Lon = initLon(-4, -1);
            let test = {
              "id": point,
              "Longitude": Lon,
              "Lattitude": Lat
            };
            var ville = new Object();
            ville.id = point;
            ville.lat = Lat;
            ville.lon = Lon;
            city.push(ville);
            city.push(ville);
          }
        }
        initPoint();

        // Fonction d'initialisation de la carte
        function initMap() {
  	       // CrÃ©er l'objet "macarte" et l'insÃ¨rer dans l'Ã©lÃ©ment HTML qui a l'ID "map"
  	       macarte = L.map('map').setView([lat, lon], 11);
           markerClusters = L.markerClusterGroup(); // Nous initialisons les groupes de marqueurs
  	       // Leaflet ne rÃ©cupÃ¨re pas les cartes (tiles) sur un serveur par dÃ©faut. Nous devons lui prÃ©ciser oÃ¹ nous souhaitons les rÃ©cupÃ©rer. Ici, openstreetmap.fr
           L.tileLayer('https://{s}.tile.openstreetmap.fr/osmfr/{z}/{x}/{y}.png', {
		           // Source des donnÃ©es
		           attribution: 'donnÃ©es Â© OpenStreetMap/ODbL - rendu OSM France',
		           minZoom: 1,
		           maxZoom: 20
  	       }).addTo(macarte);
           //CrÃ©ation du pÃ©rimÃªtre de la maison, autour du quel, la position du chien n'est pas pris en compte
           var home = L.circle([48.732675, -3.446217], {
             color: 'red',
             fillColor: '#f03',
             fillOpacity: 0.5,
             //Radius = Rayon "Maison"
             radius: 500
          }).addTo(macarte);

           //test pour ajout dans tableau city
           for (ville in city) {
             // Nous dÃ©finissons l'icÃ´ne Ã  utiliser pour le marqueur, sa taille affichÃ©e (iconSize), sa position (iconAnchor) et le dÃ©calage de son ancrage (popupAnchor)
             var myIcon = L.icon({
             iconSize: [50, 50],
             iconAnchor: [25, 50],
             popupAnchor: [-3, -76],
             });
             console.log(city[ville].lat);
             console.log(city[ville].lon);
             var marker = L.marker([city[ville].lat, city[ville].lon]).addTo(macarte);
              // Nous ajoutons la popup. A noter que son contenu (ici la variable ville) peut Ãªtre du HTML
	            //marker.bindPopup(ville);
              marker.bindPopup(`<b> ${ville} <b><br>Lattitude: ${city[ville].lat} <br>Longitude: ${city[ville].lon} <br>Altitude: `);
              //markerClusters.addLayer(marker); // Nous ajoutons le marqueur aux groupes
  	       }

           macarte.addLayer(markerClusters);
           // Nous ajoutons la popup. A noter que son contenu (ici la variable ville) peut Ãªtre du HTML
           home.bindPopup("Maison")
        }
        window.onload = function(){
		    // Fonction d'initialisation qui s'exÃ©cute lorsque le DOM est chargÃ©
		    initMap();
        };
